export default ".input-field {\n  padding: 5px;\n  border-radius: 5px 5px;\n  width: 100%;\n}\n\n.input-label {\n  display: block;\n}\n\n.btn-primary {\n  background-color: #ee352a;\n  border-color: #ee352a;\n  width: 100px;\n  border-radius: 5px 5px;\n  float: right;\n  margin-right: 15px;\n  margin-top: 15px;\n  padding: 3px;\n}\n\n.btn-primary:disabled {\n  background-color: grey;\n  border-color: grey;\n}\n\n.btn-secondary {\n  background: none;\n  border: none;\n  color: black;\n  width: 100px;\n  border-radius: 5px 5px;\n  margin-right: 15px;\n  margin-top: 15px;\n  padding: 3px;\n}\n\n.waw-checkbox {\n  display: flex;\n  align-items: center;\n  position: relative;\n  height: 25px;\n  padding-left: 35px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-size: 16px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n.waw-checkbox input {\n  position: absolute;\n  opacity: 0;\n  cursor: pointer;\n  height: 0;\n  width: 0;\n}\n\n.waw-checkbox:hover input ~ .checkmark {\n  border: 1.5px solid #30a4f7;\n}\n\n.waw-checkbox input:checked ~ .checkmark {\n  background-color: #30a4f7;\n  transition: 0.2s ease-in-out;\n}\n\n.waw-checkbox input:checked ~ .checkmark:after {\n  display: block;\n}\n\n.waw-checkbox .checkmark {\n  position: absolute;\n  top: 3px;\n  left: 0;\n  height: 22px;\n  width: 22px;\n  border-radius: 6px;\n  background-color: #fff;\n  border: 1px solid #f3f3f3;\n  transition: 0.2s ease-in-out;\n}\n\n.waw-checkbox .checkmark:after {\n  content: \"\";\n  position: absolute;\n  display: none;\n  left: 7px;\n  top: 3px;\n  width: 6px;\n  height: 11px;\n  border: solid white;\n  border-width: 0 2px 2px 0;\n  transform: rotate(45deg);\n}\n/*# sourceMappingURL=data:application/json;base64, */"