import { Component, OnInit } from '@angular/core';
import { JobService } from '@services';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-accept',
  template: `
  `,
  styles: []
})
export class AcceptComponent implements OnInit {

  constructor(public js: JobService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    const job_id = this.route.snapshot.params.job_id;
    const assigned = this.route.snapshot.params.assigned;

    this.js.accept(job_id, assigned, (res) => {
      // console.log('res', res);
    });
    this.router.navigate(['/dashboard']);
  }

}
